* {
  box-sizing: border-box;
}

.p-component {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 1rem;
  font-weight: normal;
}

.p-component-overlay {
  background-color: rgba(0, 0, 0, 0.4);
  transition-duration: 0.2s;
}

.p-disabled, .p-component:disabled {
  opacity: 0.6;
}

.p-error {
  color: #e24c4c;
}

.p-text-secondary {
  color: #6c757d;
}

.pi {
  font-size: 1rem;
}

.p-link {
  font-size: 1rem;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  border-radius: 6px;
}
.p-link:focus {
  outline: 0 none;
  outline-offset: 0;
  box-shadow: 0 0 0 0.2rem #C7D2FE;
}

.p-component-overlay-enter {
  -webkit-animation: p-component-overlay-enter-animation 150ms forwards;
          animation: p-component-overlay-enter-animation 150ms forwards;
}

.p-component-overlay-leave {
  -webkit-animation: p-component-overlay-leave-animation 150ms forwards;
          animation: p-component-overlay-leave-animation 150ms forwards;
}

@-webkit-keyframes p-component-overlay-enter-animation {
  from {
    background-color: transparent;
  }
  to {
    background-color: rgba(0, 0, 0, 0.4);
    background-color: var(--maskbg);
  }
}

@keyframes p-component-overlay-enter-animation {
  from {
    background-color: transparent;
  }
  to {
    background-color: rgba(0, 0, 0, 0.4);
    background-color: var(--maskbg);
  }
}
@-webkit-keyframes p-component-overlay-leave-animation {
  from {
    background-color: rgba(0, 0, 0, 0.4);
    background-color: var(--maskbg);
  }
  to {
    background-color: transparent;
  }
}
@keyframes p-component-overlay-leave-animation {
  from {
    background-color: rgba(0, 0, 0, 0.4);
    background-color: var(--maskbg);
  }
  to {
    background-color: transparent;
  }
}

